.text-img-section{
    @extend %NormalSection;
    .info {
        
        p{color: $BlueColor}

    }
    .img {
        text-align: center;
    }
}


// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) { 
    
    .text-img-section{
        text-align:center;
        .info {
            text-align: center;
        }
        .img {
            width: 70%;
            margin: auto;
            img{width: 100%}
        }
        p{width: 80%;margin: auto}
    }
 }

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) { 
    .text-img-section{
        text-align:center;
        .info {
            text-align: center;
        }
        .img {
            width: 80%;
            margin: auto;
            img{width: 100%}
        }
    }
 }

